import React from "react";
import { Link } from "react-router-dom";

export default function Navbar() {
  return (
    <div>
      <nav
        className="navbar fixed-top navbar-expand-lg bg-dark border-bottom border-bottom-dark"
        data-bs-theme="dark"
      >
        <div className="container-fluid d-flex justify-content-between">
          {" "}
          {/* Update this line */}
          <Link className="navbar-brand" to="/">
            <img
              src="https://cdn3.iconfinder.com/data/icons/science-indigo-vol-1/256/Forensics-512.png"
              alt="Logo"
              width="30"
              height="24"
              className="d-inline-block align-text-top"
            />
            EviSecura
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link active" to="/aboutus">
                  About Us
                </Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link active" to="/caseprofile">
                  Case Profile
                </Link>
              </li>

              <li className="nav-item">
                <Link className="nav-link active" to="/contactus">
                  Contact Us
                </Link>
              </li>
            </ul>
            <div>
              <Link
                className="nav-link active btn btn-primary"
                to="/login"
                style={{ height: "30px", padding: "5px" }}
              >
                Login/Signup
              </Link>
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
}
