import React from "react";
import Profile21 from "./Profile21";
import "./component.css";

function Profile2() {
  return (
    <div className="container bg-dark">
      <h1
        className="text-center text-white"
        style={{ textDecoration: "underline" }}
      >
        Details
      </h1>
      <div className="row container">
        <div className="col-2 bg-warning text-black rounded">
          <Profile21 />
        </div>
        <div className="col-10 text-white">
          Welcome to the Case Details page, a hub of critical insights within
          our Digital Evidence Management System. This resource provides an
          intricate tapestry of each investigation, encapsulating vital case
          information, a succinct summary, meticulously organized evidence
          inventories, traceable chains of custody, activity timelines,
          collaborative annotations, visualized patterns, related case
          connections, communication logs, and comprehensive reports. This
          comprehensive perspective empowers well-informed decisions,
          collaborative endeavors, and impeccable accountability, serving as a
          catalyst for efficient and successful case resolutions.
        </div>
      </div>
    </div>
  );
}

export default Profile2;
