import React from "react";
import "./component.css";

function Profile1() {
  return (
    <div className="container">
      <div className="row">
        <div className="col-md-4 my-3">
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/3/3e/Einstein_1921_by_F_Schmutzer_-_restoration.jpg/330px-Einstein_1921_by_F_Schmutzer_-_restoration.jpg"
            class="img-thumbnail"
            alt="..."
            style={{ width: "300px", height: "300px" }}
          />
        </div>
        <div className="col-md-8 my-3">
          <table class="table table-dark table-hover">
            <thead>
              <tr>
                <th colSpan={2} className="text-center">
                  Contact - Information
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th scope="row-1">Name</th>
                <td>Albert Einstein</td>
              </tr>
              <tr>
                <th scope="row-2">Designation</th>
                <td>Commissioner</td>
              </tr>
              <tr>
                <th scope="row-3">From Date:</th>
                <td>20-july-2000</td>
              </tr>
              <tr>
                <th scope="row-4">State</th>
                <td>Karnataka</td>
              </tr>
              <tr>
                <th scope="row-5">Contact Number:</th>
                <td>9989755892</td>
              </tr>
              <tr>
                <th scope="row-6">Email-id:</th>
                <td>einstein.albert@gmail.com</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <hr />
      <hr />
    </div>
  );
}

export default Profile1;
